.input {
  margin: 2rem auto;
  padding: 1rem;
  width: 90%;
  max-width: 40rem;
}

.input label {
  display: block;
  font-weight: 400;
  margin-bottom: 0.5rem;
}

.input input {
  font: inherit;
  display: block;
  width: 100%;
  border: 2px solid #ccc;
  border-radius: 5px;
  padding: 0.3rem;
  margin-bottom: 0.5rem;
}

.input input:focus {
  outline: none;
  border-color: #219EBC;
}